	padding: 0 40px;
}
@-webkit-keyframes rounded {
0%
{
	opacity:0;
	-webkit-transform: scale(0.5);
	transform: scale(0.5);
}
50%
{
	opacity:1;
}
100%
{
	opacity:0;
	-webkit-transform: scale(1.2);
	transform: scale(1.2);
}
}
@keyframes rounded {
0%
{
	opacity:0;
	-webkit-transform: scale(0.5);
	transform: scale(0.5);
}
50%
{
