.ripple_effect_right_top {
	position: absolute;
    top: 0;
    right: 0;
    width: 700px;
    height: 700px;
	overflow: hidden;
}
.ripple_effect_right_top .circle_bg1 {
    border-radius: 100%;
    height: 100%;
    width: 100%;
    position: absolute;
    right: -350px;
    top: -350px;
    left: auto;
    bottom: auto;
}
.circle_bg1 {
    border-radius: 100%;
    height: 100%;
    width: 100%;
    position: absolute;
    left: -350px;
    bottom: -350px;
}
.circle_bg1 span{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 50%;
    height: 100%;
    width: 100%;
	-webkit-transform: scale(0);
	transform: scale(0);
	-webkit-animation-name:ripple;
	animation-name:ripple;
	-webkit-animation-duration:4s;
	animation-duration:4s;
	-webkit-animation-delay:6s;
	animation-delay:6s;
	-webkit-animation-iteration-count:infinite;
	animation-iteration-count:infinite;
	-webkit-animation-timing-function:linear;
	animation-timing-function:linear;
	border:1px solid rgba(255,255,255,0.2);
}
.circle_bg1 span:nth-child(2) {
	-webkit-animation-delay:5s;
	animation-delay:5s;
}
.circle_bg1 span:nth-child(3) {
	-webkit-animation-delay:4s;
	animation-delay:4s;
}
.circle_bg1 span:nth-child(4) {
	-webkit-animation-delay:3s;
	animation-delay:3s;
}
.circle_bg1 span:nth-child(5) {
	-webkit-animation-delay:2s;
	animation-delay:2s;
}
.circle_bg1 span:nth-child(6) {
	-webkit-animation-delay:1s;
	animation-delay:1s;
}

@-webkit-keyframes ripple{
  0% {-webkit-transform:scale(0.1);transform:scale(.01); opacity:1;}
  50% {-webkit-transform:scale(.5);transform:scale(.5); opacity:1;}
  100% {-webkit-transform:scale(1);transform:scale(1); opacity:0;}
}
@keyframes ripple{
  0% {-webkit-transform:scale(0.1);transform:scale(0.1);transform:scale(0.1); opacity:1;}
  50% {-webkit-transform:scale(.5);transform:scale(.5);transform:scale(.5); opacity:1;}
  100% {-webkit-transform:scale(1);transform:scale(1);transform:scale(1); opacity:0;}
}

